.ProjectCard {
  border: 1px solid #D4D4D4;
  border-radius: 0.5em;
  width: 22em;
  height: 45em;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin: 3em;
}

.ProjectCard img {
  width: 20em;
  height: 20em;
  border-bottom: 1px solid #D4D4D4;
  padding: 1em;
}

.ProjectCard .content {
    padding: 2em;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    flex-grow: 1;
}

@media screen and (max-width: 1700) {
  .ProjectCard {
    transform: scale(0.3)
  }
}