@import url('https://fonts.googleapis.com/css?family=Source+Code+Pro:300,400');

body {
  margin: 0;
  padding: 0;
  font-family: 'Source Code Pro', monospace, sans-serif;
  font-weight: 300;
  overflow-x: hidden;
}

main {
    display: flex;
    flex-direction: column;
    align-items: center;
}

section, article {
  width: 80vw;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}

article * {
  padding: 0 1em;
}

nav {
    width: 100vw;
    display: flex;
    justify-content: space-around;
    border-bottom: 1px solid #80deea;
}

a {
    text-decoration: none;
    color: black;
}

p {
  font-size: 1.2em;
}

nav a {
  margin: 2em;
}

a:hover {
  color: #80deea;
}

h1 {
    margin: 0.5em;
    font-size: 4rem;
    font-weight: 300;
    height: fit-content;
    width: fit-content;
    padding-left: 0.1em;
    border-right: 0.1em solid #80deea;
    animation: blink-caret 0.75s step-end infinite;
}

h2 {
  font-size: 2.5rem;
  font-weight: 300;
}

h4 {
  font-size: 1.5em;
  border-bottom: 1px solid #80deea;
  width: 100%;
}

h2, h4 {
  margin: 0;
}

h5 {
  margin-left: auto;
}

img {
  object-fit: cover;
}

span {
  font-size: 2.5rem;
  margin: 1em;
}

@keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: #80deea;
  }
}

.material-icons {
  font-size: 4rem;
  margin-right: 0.5em;
  top: 20px;
  left: 20px;
  position: relative;
}

@media screen and (max-width: 1700px) {
  h1 {
    font-size: 3em;
  }
  section {
    justify-content: center;
  }
}

@media screen and (max-width: 1150px) {
  section {
    flex-direction: column;
    flex-wrap: nowrap;
    align-items: center;
    width: 100vw;
  }
  h1 {
    font-size: 2.5em;
  }
}

@media screen and (max-width: 900px) {
  span {
    font-size: 1.75rem;
  }
  .material-icons {
    font-size: 2.5rem;
    top: 15px;
    left: 15px;
  }
}

@media screen and (max-width: 600px) {
  span {
    font-size: 1.1rem;
  }
  .material-icons {
    font-size: 1em;
    margin-right: 0.25em;
    top: 4px;
    left: 4px;
  }
}